import React from 'react';
import { mapStateToProps, mapDispatchToProps } from '../../redux/actionCreator';
import { connect } from 'react-redux';
import { useEffect, useState } from 'react';
import Header from '../../component/header/header'
import './map.scss';
import { Button, message } from 'antd';
import { useHistory } from 'react-router-dom';


function _Map(props) {
    const history = useHistory();
    const [loading, setLoading] = useState(false);
    const [map, setMap] = useState(null)

    /**
     * @param {loding} status 
     */
    function isLoadingFn(status) {
        setTimeout(() => {
            setLoading(status);
        }, 400);
    }



    // 
    async function initFn() {
        // window.location.reload();
        // setTimeout(() => {
        // document.getElementById('mapPage').contentWindow.location.reload();
        // }, 200);

    }

    useEffect(() => {
        window.addEventListener('message', function (event) {
            // 
            var loc = event.data;
            if (loc && loc.module == 'locationPicker') {//postmodule'locationPicker'
                let location = {
                    cityname: loc.cityname,
                    address: loc.poiaddress,
                    address_desc: loc.poiname,
                    location: loc.latlng
                }
                setMap(location);
                console.log('location', location);
            }
        }, false);
        initFn();
        // eslint-disable-next-line 
    }, [])
    function onSave() {
        message.loading({
            content: '....', duration: 0,
        })

        props.setLocaltion(map);

        setTimeout(() => {
            message.destroy();
            history.push('/integral');
        }, 200);
    }



    return (
        <div className='map_wrap' >
            <Header />
            {
                <> <div className='select_wrap'>
                    <iframe id='mapPage' className='mapPage' frameBorder={0}
                        src={`https://apis.map.qq.com/tools/locpicker?search=1&type=1&radius=1000&total=5&coord=${props._localtion?.location}&key=4I7BZ-HIS3P-ELWD3-L2R4G-MOYBJ-OLBXZ&referer=myapp`}>
                    </iframe>

                </div>
                    <div className='btns'>
                        <Button onClick={() => { history.goBack() }}></Button>
                        <Button type="primary" onClick={onSave}></Button>
                    </div>
                </>
            }

        </div>
    )
}
export default connect(mapStateToProps, mapDispatchToProps)(_Map)